import camelCase from 'lodash/camelCase'
import R, { __, assoc, concat, merge, over, pipe, reduce } from 'ramda'
import { createAction, handleActions } from 'redux-actions'
import { createSelector } from 'reselect'

import { argList, isNilOrEmpty, normalizeBy, override } from './ramda-extra'
import { define as defineValidator } from './validator'

// helper functions
const withPrefix = pipe(
  argList,
  R.ifElse(pipe(R.head, isNilOrEmpty), R.always(''), R.join('')),
  concat
)

const reducerLens = R.lensProp('reducers')
const selectorLens = R.lensProp('selectors')

// builders
const DEFAULT_OPTIONS = {
  namespace: {
    domain: '',
    path: [],
  },
  initial: {},
  actions: {},
  reducers: {},
  selectors: {},
}

const initial = state => assoc('initial', Object.freeze(state))
const withNamespace = ns => R.evolve({ namespace: override(ns) })

const defineAction = ({ domain }, name) =>
  R.assocPath(
    ['actions', camelCase(name)],
    createAction(withPrefix(domain, '/')(name))
  )

const actionGroup = (prefix, names) => config =>
  reduce(
    (acc, name) => defineAction(config.namespace, name)(acc),
    config,
    R.isEmpty(prefix) ? names : R.map(withPrefix(prefix, '_'), names)
  )

const handle = makeHandlers => config =>
  over(reducerLens, override(makeHandlers(config)), config)

const handleListActions = ({ actions }) => ({
  [actions.listPending]: override({ error: null, pending: true }),

  [actions.listComplete]: {
    next: (state, { payload: items }) => {
      const { keys: ids, itemsByKey: itemsMap } = normalizeBy('id', items)
      return merge(state, { error: null, pending: false, ids, itemsMap })
    },

    throw: (state, { payload: error }) =>
      merge(state, { error, pending: false }),
  },
})

const handleFormActions = ({ actions, initial: initialState, validator }) => ({
  [actions.formInput]: (state, { payload }) => {
    const path = payload.field.split('.')
    const fields = R.assocPath(path, payload.value, state.fields)
    const errors = validator.checkField(path, fields)
    const fieldErrors = R.assocPath(path, errors, state.fieldErrors)
    return merge(state, { fields, fieldErrors })
  },

  [actions.formReset]: R.always(initialState),

  [actions.formSubmitPending]: override({ error: null, pending: true }),
  [actions.formSubmitComplete]: {
    next: R.always(initialState),
    throw: (state, { payload: error }) =>
      merge(state, { error, pending: false }),
  },
})

const select = makeSelectors => config =>
  over(selectorLens, override(makeSelectors(config)), config)

const defineReducers = config =>
  over(reducerLens, R.curry(handleActions)(__, config.initial), config)

// states
const define = (namespace, builders) =>
  pipe(
    concat([
      withNamespace(namespace),
      select(({ namespace: ns }) => ({
        getState: R.path(ns.path),
        getStateProp: key => R.path(R.append(key, ns.path)),
      })),
    ]),
    reduce((config, builder) => builder(config), DEFAULT_OPTIONS),
    defineReducers
  )(builders)

const makeForm = (namespace, options = {}, extraBuilders = []) =>
  define(
    namespace,
    concat(
      [
        initial({
          error: null,
          fields: options.fields,
          fieldErrors: {},
          pending: false,
        }),
        actionGroup('FORM', ['INPUT', 'RESET']),
        actionGroup('FORM_SUBMIT', ['REQUESTED', 'PENDING', 'COMPLETE']),
        assoc('validator', options.validator || defineValidator({ spec: {} })),
        handle(handleFormActions),
        select(({ selectors, validator }) => {
          const { getStateProp } = selectors

          return {
            getError: getStateProp('error'),
            getFields: getStateProp('fields'),
            getFieldErrors: getStateProp('fieldErrors'),
            isPending: getStateProp('pending'),
            isValid: pipe(getStateProp('fields'), validator.allValid),
          }
        }),
      ],
      extraBuilders
    )
  )

const makeList = (namespace, extraBuilders = []) =>
  define(
    namespace,
    concat(
      [
        initial({ error: null, ids: [], itemsMap: {}, pending: false }),
        actionGroup('LIST', ['REQUESTED', 'PENDING', 'COMPLETE']),
        handle(handleListActions),
        select(({ selectors }) => {
          const { getStateProp } = selectors

          const basic = {
            getError: getStateProp('error'),
            getIds: getStateProp('ids'),
            getItemsMap: getStateProp('itemsMap'),
            isPending: getStateProp('pending'),
          }

          const computed = {
            getItems: createSelector(
              [basic.getIds, basic.getItemsMap],
              R.props
            ),
          }

          return merge(basic, computed)
        }),
      ],
      extraBuilders
    )
  )

export { actionGroup, define, handle, initial, makeForm, makeList, select }
